@use '../util' as *;
// https://codepen.io/SaraSoueidan/pen/jpBbrq/?editors=1100

.toggle {
  display: grid;
  grid-template-columns: 1fr rem(60) 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: rem(4.8) rem(8);
  border: none;
  margin: 0;
  label {
    font-size: rem(14);
    align-self: center;
    &[for='dark'] {
      grid-column: 1 / 2;
      grid-row: 1/2;
      justify-self: end;
    }

    &[for='system'] {
      align-self: start;
      grid-column: 1/4;
      grid-row: 2/3;
      justify-self: center;
    }

    &[for='light'] {
      grid-column: 3/4;

    }
  }

  &__wrapper {
    height: rem(24);
    position: relative;
  }

  input[type='radio'] {
    opacity: 0;
    margin: 0 -2px 0 -2px;
    width: rem(20);
    height: rem(24);

    &:focus ~ .toggle__button{
    border: 2px solid white;
    }
  } 

  &__background {
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    border-radius: rem(12);
    overflow: hidden;
    display: block;
    height: 100%;
    background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
    pointer-events: none;
  }

  &__button {
    position: absolute;
    top: rem(3);
    left: rem(3);
    right: 100%;
    width: rem(18);
    height: rem(18);
    border-radius: 50%;
    background-color: var(--dark-bg);
  }
  #light:checked ~ .toggle__button{
    left: calc(100% - 21px);
    right: 0;
  }
  #system:checked ~ .toggle__button{
    left: 50%;
    right: auto;
    transform: translate(-50%);
  }
}